import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import { API_BASE_URL } from "@/config";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartProducts: [
        ],
        userAccessKey: null,
        cartProductsData: [],
        colors: [],
        cartLoad: false,
        orderInfo: null
    },
    mutations: {
        updateOrderInfo(state, orderInfo) {
            state.orderInfo = orderInfo;
        },
        clearOrder(state) {
            state.cartProducts = [],
                state.cartProductsData = []
        },
        updateAmount(state, { productId, amount }) {
            const item = state.cartProducts.find(item => item.productId === productId)

            if (item) {
                item.amount = amount;
            }
        },
        cartLoading(state, value) {
            state.cartLoad = value;
        },
        deleteProduct(state, productId) {
            state.cartProducts = state.cartProducts.filter(item => item.productId !== productId)
        },
        updateUserAccessKey(state, accessKey) {
            state.userAccessKey = accessKey;
        },
        updateCartProductsData(state, items) {
            state.cartProductsData = items;
        },
        syncCartProdducts(state) {
            state.cartProducts = state.cartProductsData.map(item => {
                return {
                    productId: item.product.id,
                    amount: item.quantity
                }
            })
        }
    },
    getters: {
        cartDetailsProducts(state) {
            return state.cartProducts.map(item => {
                const product = state.cartProductsData.find(p => p.product.id === item.productId).product;
                return {
                    ...item,
                    product: {
                        ...product,
                        img: product.image.file.url
                    }
                }
            })
        },
        cartTotal(state, getters) {
            return getters.cartDetailsProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
        }
    },
    actions: {
        loadOrderInfo(context, orderId) {
            return axios
                .get(API_BASE_URL + '/api/orders/' + orderId, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response => {
                    context.commit('updateOrderInfo', response.data)
                })
        },
        loadCart(context) {
            return (new Promise(resolve => setTimeout(resolve, 200)))
                .then(() => {
                    return axios
                        .get(API_BASE_URL + '/api/baskets', {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            }
                        })
                        .then(response => {
                            if (!context.state.userAccessKey) {
                                localStorage.setItem('userAccessKey', response.data.user.accessKey)
                                context.commit('updateUserAccessKey', response.data.user.accessKey)
                            }
                            context.commit('updateCartProductsData', response.data.items)
                            context.commit('syncCartProdducts')
                        })
                })
        },
        addProductToCart(context, { productId, amount }) {
            return (new Promise(resolve => setTimeout(resolve, 500)))
                .then(() => {
                    return axios
                        .post(API_BASE_URL + '/api/baskets/products', {
                            productId: productId,
                            quantity: amount
                        }, {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            }
                        })
                        .then(response => {
                            context.commit('updateCartProductsData', response.data.items)
                            context.commit('syncCartProdducts')
                        })
                })
        },
        updateCartProductAmount(context, { productId, amount }) {
            context.commit('updateAmount', { productId, amount })
            if (amount < 1) {
                return
            }
            return axios
                .put(API_BASE_URL + '/api/baskets/products', {
                    productId: productId,
                    quantity: amount
                }, {
                    params: {
                        userAccessKey: context.state.userAccessKey
                    }
                })
                .then(response => {
                    context.commit('updateCartProductsData', response.data.items)
                })
                .catch(() => {
                    context.commit('syncCartProdducts')
                })
        },
        deleteFromCart(context, productId) {
            return (new Promise(resolve => setTimeout(resolve, 300)))
                .then(() => {
                    return axios
                        .delete(API_BASE_URL + '/api/baskets/products', {
                            params: {
                                userAccessKey: context.state.userAccessKey
                            },
                            data: {
                                productId
                            }
                        })
                        .then(response => {
                            context.commit('deleteProduct', response.data.items)
                            context.commit('updateCartProductsData', response.data.items)
                            context.commit('syncCartProdducts')
                        })
                        .catch(() => context.commit('syncCartProdducts'))
                })

        }

    }
});