<template>
    <aside class="filter">
        <h2 class="filter__title">Фильтры</h2>

        <form class="filter__form form" action="#" method="get" @submit.prevent="submit()">
            <fieldset class="form__block">
                <legend class="form__legend">Цена</legend>
                <label class="form__label form__label--price">
                    <input class="form__input" type="text" name="min-price" v-model.number="currentProductsFrom">
                    <span class="form__value">От</span>
                </label>
                <label class="form__label form__label--price">
                    <input class="form__input" type="text" name="max-price" v-model.number="currentProductsTo">
                    <span class="form__value">До</span>
                </label>
            </fieldset>

            <fieldset class="form__block">
                <legend class="form__legend">Категория</legend>
                <label class="form__label form__label--select">
                    <select class="form__select" type="text" v-model.number="currentCategoryId">
                        <option value="0">Все категории</option>
                        <option v-for="category in categories" :key="category.id" :value="category.id">{{ category.title }}
                        </option>

                    </select>
                </label>
            </fieldset>

            <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors">
                    <li class="colors__item" v-for="color in colors">
                        <label class="colors__label">
                            <input class="colors__radio sr-only" v-model.number="currentColor" type="radio" :value="color.id">
                            <span class="colors__value" :style="{ backgroundColor: color.code }">
                            </span>
                        </label>
                    </li>
                </ul>
            </fieldset>

            <fieldset class="form__block">
                <legend class="form__legend">Объемб в ГБ</legend>
                <ul class="check-list">
                    <li class="check-list__item">
                        <label class="check-list__label">
                            <input class="check-list__check sr-only" type="checkbox" name="volume" value="8">
                            <span class="check-list__desc">
                                8
                                <span>(313)</span>
                            </span>
                        </label>
                    </li>
                </ul>
            </fieldset>

            <button class="filter__submit button button--primery" type="submit">
                Применить
            </button>
            <button @click.prevent="reset()" class="filter__reset button button--second" type="button">
                Сбросить
            </button>
        </form>
    </aside>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';


export default {
    data() {
        return {
            currentProductsFrom: 0,
            currentProductsTo: 0,
            currentCategoryId: 0,
            categoriesData: null,
            currentColor: null,
        }
    },
    props: ['priceTo', 'priceFrom', 'categoryId', 'colors', 'products', 'filterProductColor'],
    computed: {
        categories() {
            return this.categoriesData
                ? this.categoriesData.items
                : [];
        }
    },
    watch: {
        categoryId(value) {
            this.currentCategoryId = value;
        },
        priceFrom(value) {
            this.currentProductsFrom = value;
        },
        priceTo(value) {
            this.currentProductsTo = value;
        }
    },
    methods: {
        submit() {
            this.$emit('update:filterProductColor', this.currentColor);
            this.$emit('update:priceTo', this.currentProductsTo);
            this.$emit('update:priceFrom', this.currentProductsFrom);
            this.$emit('update:categoryId', this.currentCategoryId);
        },
        reset() {
            this.$emit('update:priceTo', 0);
            this.$emit('update:priceFrom', 0);
            this.$emit('update:categoryId', 0);
        },
        loadCategories() {
            axios.get(API_BASE_URL + `/api/productCategories`)
                .then(response => this.categoriesData = response.data);
        }
    },
    created() {
        this.loadCategories();
    }
}
</script>
