<template>
  <router-link class="header__cart" :to="{ name: 'cart' }" aria-label="Корзина с товарами">


    <svg width="30" height="21" fill="currentColor">
      <use xlink:href="#icon-cart"></use>
    </svg>
    <img v-if="loading" src="/img/loader.gif" alt="">
    <span v-else class="header__count" aria-label="Количество товаров">{{ $store.state.cartProducts.length }}</span>
  </router-link>
</template>
<script>

export default {
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    load() {
      return this.$store.state.cartLoad
    }
  },
  watch: {
    load() {
      this.loading = this.$store.state.cartLoad;
    }
  }
  
}
</script>

<style scoped>
  img {
    position: absolute;
    top: 0;
    right: 0;
  }
</style>