<template>
    <ul class="catalog__pagination pagination">
        <li class="pagination__item">
            <a @click.prevent="prevPage(page)" href="#" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === 1}" aria-label="Предыдущая страница">
                <svg width="8" height="14" fill="currentColor">
                    <use xlink:href="#icon-arrow-left"></use>
                </svg>
            </a>
        </li>
        <li class="pagination__item" v-for="pageNum in pages" :key="pageNum">
            <a href="#" @click.prevent="paginate(pageNum)" class="pagination__link" :class="{'pagination__link--current': pageNum === page}">
                {{ pageNum }}
            </a>
        </li>
        <li class="pagination__item">
            <a href="#" @click.prevent="nextPage(page)" class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': page === pages}"  aria-label="Следующая страница">
                <svg width="8" height="14" fill="currentColor">
                    <use xlink:href="#icon-arrow-right"></use>
                </svg>
            </a>
        </li>
    </ul>
</template>
<script>
export default {
    props: ['count', 'perPage', 'page'],
    model: {
        prop: 'page',
        event: 'paginate'
    },
    computed: {
        pages() {
            return Math.ceil(this.count / this.perPage)
        }
    },
    methods: {
        paginate(page) {
            this.$emit('paginate', page)
        },
        nextPage(page) {
            this.$emit('paginate', page + 1);
        },
        prevPage(page) {
            this.$emit('paginate', page - 1)
        }         
    }
}
</script>
