<template>
    <main class="content container">
        <div class="content__top">
            <ul class="breadcrumbs">
                <li class="breadcrumbs__item">
                    <router-link :to="{ name: 'main' }" class="breadcrumbs__link" href="index.html">
                        Каталог
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <router-link :to="{ name: 'cart' }" class="breadcrumbs__link" href="cart.html">
                        Корзина
                    </router-link>
                </li>
                <li class="breadcrumbs__item">
                    <a class="breadcrumbs__link">
                        Оформление заказа
                    </a>
                </li>
            </ul>

            <h1 class="content__title">
                Корзина
            </h1>
            <span class="content__info">
                3 товара
            </span>
        </div>

        <section class="cart">
            <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
                <div class="cart__field">
                    <div class="cart__data">
                        <BaseFormText title="ФИО" v-model="formData.name" placeholder="Введите Имя"
                            :error="formError.name" />
                        <BaseFormText title="Адрес" v-model="formData.address" placeholder="Введите адрес доставки"
                            :error="formError.address" />
                        <BaseFormText title="Телефон" type="tel" v-model="formData.phone"
                            placeholder="Введите номер телефона" :error="formError.phone" />
                        <BaseFormText title="Email" type="email" v-model="formData.email" placeholder="Введите Email"
                            :error="formError.email" />
                        <BaseFormTextarea title="Комментарий" v-model="formData.comment"
                            placeholder="Ваши пожелания к заказу" :error="formError.comment" />

                    </div>

                    <div class="cart__options">
                        <h3 class="cart__title">Доставка</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="delivery" value="0">
                                    <span class="options__value">
                                        Самовывоз <b>бесплатно</b>
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                                    <span class="options__value">
                                        Курьером <b>500 ₽</b>
                                    </span>
                                </label>
                            </li>
                        </ul>

                        <h3 class="cart__title">Оплата</h3>
                        <ul class="cart__options options">
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="pay" value="card">
                                    <span class="options__value">
                                        Картой при получении
                                    </span>
                                </label>
                            </li>
                            <li class="options__item">
                                <label class="options__label">
                                    <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                                    <span class="options__value">
                                        Наличными при получении
                                    </span>
                                </label>
                            </li>
                        </ul>
                    </div>
                </div>

                <div class="cart__block">
                    <ul class="cart__orders">

                        <OrderItems v-for="item in products" :key="item.productId" :item="item"></OrderItems>

                    </ul>

                    <div class="cart__total">
                        <!-- <p>Доставка: <b>500 ₽</b></p> -->
                        <p>Итого: <b>{{ products.length }}</b> товара на сумму <b> {{ totalPrice | numberFormat }} ₽</b></p>
                    </div>

                    <button class="cart__button button button--primery" type="submit">
                        Оформить заказ
                    </button>
                    <div v-if="formLoading" class="ofo">Оформляем заказ...</div>
                </div>
                <div class="cart__error form__error-block" v-if="formErrorMsg">
                    <h4>Заявка не отправлена!</h4>
                    <p>
                        {{ formErrorMsg }}
                    </p>
                </div>
            </form>
        </section>
    </main>
</template>
<script>

import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import { API_BASE_URL } from '@/config';
import axios from 'axios';
import { mapGetters, mapActions } from 'vuex';
import numberFormat from '@/helpers/numberFormat';
import OrderItems from '@/components/OrderItems.vue';


export default {
    data() {
        return {
            formData: {},
            formError: {},
            formErrorMsg: '',
            formLoading: false
        }
    },
    filters: {
        numberFormat
    },
    components: { BaseFormText, BaseFormTextarea, OrderItems },
    computed: {
        ...mapGetters(
            { products: 'cartDetailsProducts', totalPrice: 'cartTotal' }),

    },
    methods: {
        order() {
            this.formLoading = true;
            return (new Promise(resolve => setTimeout(resolve, 1000)))
                .then(() => {
                    this.formError = {}
                    return axios
                        .post(API_BASE_URL + '/api/orders', {
                            ...this.formData
                        }, {
                            params: {
                                userAccessKey: this.$store.state.userAccessKey
                            }
                        })
                        .then(response => {
                            this.$store.commit('clearOrder'),
                                this.$store.commit('updateOrderInfo', response.data);
                            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } })
                            this.formLoading = false;
                        })
                        .catch(error => {
                            this.formError = error.response.data.error.request || {};
                            this.formErrorMsg = error.response.data.error.message;
                            this.formLoading = false;
                        })
                })
        }
    },

}

</script>

<style scoped>
.ofo {
    margin: 25px auto 0;
    text-align: center;
}
</style>

